export default {
	props:{
		post: {
			type: Object,
			required: true
		},
	},
	data() {
		return {
			valid:false,
			content:'',
			
		}
	},
	computed:{
		me() {
			return this.$store.state.users.me;
		}
	},
	methods:{
		onSubmitComment() {
			if(this.$refs.form.validate()) {
				this.$store.dispatch('posts/addComment', {
					content:this.content,
					author: 'Test',
					postId: this.post.id,
				})
				.then(() => {
					this.$refs.form.reset()
				})
				.catch((err) => {
					console.log(err);
				})
			}
		},
		onCommentsToggle() {
			this.$emit('onCommentsToggle');
		}
	}
}
