	import CommentForm from "~/components/CommentForm"
	
export default {
	props: {
		post: {
			type: Object,
			required: true
		},
	},
	data(){
		return{
			menu:false
		}
	},
	computed: {
		mainPosts() {
			return this.$store.state.posts.mainPosts;
		},
	},
	methods: {
		onDeleteTweet() {
			this.$store.dispatch('posts/removeMainPosts', {
				id: this.post.id
			})
			.then(() => {
				return;
			})
			.catch((err) => {
				console.log(err);
			});
		},
		onRouteTweetPage() {
			return this.$router.push({
				path:`/post/${this.post.id}`
			})
		},
		onRouteAuthor() {
			console.log('hi');
			return this.$router.push({
				path:`/user/${this.post.author.id}`
			})
		},
	},
	components:{
		CommentForm,
	}
}
